import React from 'react'
import { StaticImage } from 'gatsby-plugin-image';
import { AiOutlineMenu, AiOutlineClose } from "react-icons/ai";
import {
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerContent,
  useDisclosure,
  Icon
} from "@chakra-ui/react"

import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Link from 'components/Link';
import Button from 'components/Button';
import theme, { responsive } from '../components/ThemeProvider/theme';

const Header = ({ siteTitle, ...props }) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  return (
    <Flex
      as="header"
      position="fixed"
      bg="white"
      top={0}
      left={0}
      right={0}
      alignItems="center"
      justifyContent="space-between"
      zIndex="docked"
      {...props}
    >
      <Box.Relative width="11.25em" mx={responsive('1em', '2em')}>
        <Text.H1>
          <Link to="/">
            <StaticImage src="logo.png" alt="logo" />
          </Link>
        </Text.H1>
      </Box.Relative>
      <Box.Relative
        as={Button}
        borderRadius="0"
        bg={isOpen ? 'custom.blue' : 'white'}
        color={isOpen ? 'white' : 'custom.blue'}
        _hover={{
          bg: 'custom.blue',
          color: 'white',
        }}
        _focus={{
          bg: 'white',
          color: 'custom.blue',
        }}
        onClick={onOpen}
        height={theme.headerHeight}
        width={theme.headerHeight}
        px="0"
      >
        <Icon as={isOpen ? AiOutlineClose : AiOutlineMenu} w={responsive('1.25rem', '2rem')} h={responsive('1.25rem', '2rem')} />
      </Box.Relative>
      <Drawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
        size="md"
        preserveScrollBarGap
      >
        <DrawerContent bg="custom.blue" style={{ top:theme.headerHeight }} height="auto">
          <DrawerBody height="100%">
          </DrawerBody>
          <DrawerFooter>
            <Button
              as={Link}
              width="100%"
              bg="custom.orange"
              fontSize={responsive('1em', '1.25em')}
              py="1rem"
              _hover={{ bg: 'custom.orange' }}
              href="#"
            ></Button>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
    </Flex>
  )
}

export default Header
